<template>
      <div class="row">
        <card class="card-signup" header-classes="text-center" color="orange">
          <template slot="header">
            <h3 class="card-title title-up"> </h3>
          </template>
          <template>
              <fg-input
                v-model="email"
                class="no-border"
                placeholder=""
                addon-left-icon="now-ui-icons ui-1_email-85"
                type="email"
              >
              </fg-input>

              <form>
              <fg-input
                v-model="pw"
                class="no-border input-lg"
                addon-left-icon="now-ui-icons ui-1_lock-circle-open"
                placeholder=""
                type="password"
                autocomplete="off"
              >
              </fg-input>

              <fg-input
                v-model="pwCheck"
                class="no-border input-lg"
                addon-left-icon="now-ui-icons ui-1_lock-circle-open"
                placeholder=" "
                type="password"
                autocomplete="off"
              >
              </fg-input>
              </form>

          </template>
          <div class="card-footer text-center">
            <n-button type="neutral" round size="lg" @click="signUp"></n-button>
          </div>
        </card>
      </div>
</template>
<script>
import { Card, FormGroupInput, Button } from '@/components';
import firebase from 'firebase';

export default {
  name: 'signupForm',
  components: {
    Card,
    [Button.name]: Button,
    [FormGroupInput.name]: FormGroupInput
  },
  data() {
    return {
      email:'',
      pw:'',
      pwCheck:'',
    }
  },
  methods: {
    signUp() {
      firebase.auth().createUserWithEmailAndPassword(this.email, this.pw)
        .then(
          user => {
            console.log("  >>", user);
            location.reload();
          },
          function(err) {
            alert(": " + err.message);
          }
        )
    }
  },
};
</script>
<style></style>
